<template>
  <div id="oneheader">
    <div class="container">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <div class="col-xs-12 col-sm-6 column">
            <img
              src="../../static/images/one_header/logo1.jpg"
              alt="logo"
              title=""
              class="img-responsive img-rounded center-block"
            >
          </div>
          <div class="col-xs-12 col-sm-6 column">
            <h1></h1>
            <article style="text-indent:2em;">
              {{aboutMsg}}
            </article>
          </div>
 
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'oneheader',
  data() {
    return {
      aboutMsg: ''
    };
  }
};
</script>

<style scoped>
#one_header {
  margin-bottom: 1em;
}
</style>



